<template>
  <div class="selectCountry">
        <div class="selectCountItem afterbutn" v-on:click="clickdisplayitem">
          <div class="selectCountryImg">
            <img :src="get_LANGUAGE.IMG" alt="" style="width: 100%" />
          </div>
          <div class="selectCountryName">{{ get_LANGUAGE.NAME }}</div>
        </div>
        <div
          class="listCount"
          :style="LANGUAGE_IS_DOW ? 'display:block;' : 'display:none;'"
        >
          <div
            class="selectCountItem listCountItem1"
            v-for="item in SELECT_COUNTRY_LANGUAGE"
            :key="item.type"
            v-on:click="clickitem(item)"
          >
            <div class="selectCountryImg">
              <img :src="item.IMG" alt="" style="width: 100%" />
            </div>
            <div class="selectCountryName">{{ item.NAME }}</div>
          </div>
        </div>
      </div>
</template>

<script>
export default {
  computed: {
    get_LANGUAGE() {
      let selectLanguage = SELECT_COUNTRY_LANGUAGE.filter((item) => {
        return item.type == this.$store.state.language;
      });
      return selectLanguage[0];
    },
  },
  data() {
    return {
      LANGUAGE_IS_DOW: false,
      SELECT_COUNTRY_LANGUAGE,
    };
  },
  methods: {
   ...mapActions([
     ACTION_TYPES.UPDATE_LANGUAGE,
    ]),
    clickitem(item) {
      this[ACTION_TYPES.UPDATE_LANGUAGE]({ language: item.type });
      this.LANGUAGE_IS_DOW = !this.LANGUAGE_IS_DOW;
    },
    clickdisplayitem() {
      this.LANGUAGE_IS_DOW = !this.LANGUAGE_IS_DOW;
    },
  }
  
}
import { mapActions } from "vuex";
import {
  ACTION_TYPES,
  SELECT_COUNTRY_LANGUAGE,
} from "@/util/constants";
</script>

